import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { govern } from '../../menus';

/**
 * Generated class for the MobilemoneyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-government',
  templateUrl: 'government.html',
})
export class GovernmentPage {

  langs:any = {};
  govaList:any;
  
  utilities: any = [];
  constructor( public alertService: AlertServiceProvider,public navCtrl: NavController,public globalVars: GlobalVarsProvider,
     public navParams: NavParams) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() ); 
    this.utilities = govern;
  
  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad GovernmentPage');
  }
  goHome(){
    this.navCtrl.setRoot('HomePage');
  }

  openpage( dat ) {
       console.log("*****************",dat);

       if(dat.name == 'znzotherinstitutions'){
  console.log("*****************here1",dat);
     this.navCtrl.push( 'GovernmentPage2');
       }else{
  console.log("*****************here2",dat);

    this.navCtrl.push( 'CollectionPage', { data: dat } );
       }
  }

}



